@font-face {
  font-family: "antonio-bold";
  src: url("fonts/antonio/antonio-bold-webfont.woff2") format("woff2"), url("fonts/antonio/antonio-bold-webfont.woff") format("woff");
}
@font-face {
  font-family: "antonio-light";
  src: url("fonts/antonio/antonio-light-webfont.woff2") format("woff2"), url("fonts/antonio/antonio-light-webfont.woff") format("woff");
}
@font-face {
  font-family: "antonio-regular";
  src: url("fonts/antonio/antonio-regular-webfont.woff2") format("woff2"), url("fonts/antonio/antonio-regular-webfont.woff") format("woff");
}
@font-face {
  font-family: "great-vibes-regular";
  src: url("fonts/great_vibes/great-vibes-regular-webfont.woff2") format("woff2"), url("fonts/great_vibes/great-vibes-regular-webfont.woff") format("woff");
}
@font-face {
  font-family: "lora-bold-italic";
  src: url("fonts/lora/lora-bold-italic-webfont.woff2") format("woff2"), url("fonts/lora/lora-bold-italic-webfont.woff") format("woff");
}
@font-face {
  font-family: "lora-bold";
  src: url("fonts/lora/lora-bold-webfont.woff2") format("woff2"), url("fonts/lora/lora-bold-webfont.woff") format("woff");
}
@font-face {
  font-family: "lora-italic";
  src: url("fonts/lora/lora-italic-webfont.woff2") format("woff2"), url("fonts/lora/lora-italic-webfont.woff") format("woff");
}
@font-face {
  font-family: "lora-regular";
  src: url("fonts/lora/lora-regular-webfont.woff2") format("woff2"), url("fonts/lora/lora-regular-webfont.woff") format("woff");
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
body {
  line-height: 1;
  background-color: #fff;
  text-rendering: optimizeLegibility;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
}
:focus {
  outline: none;
}
p,
dd,
dl,
figure,
blockquote {
  margin: 0;
}
blockquote,
q {
  quotes: none;
}
ul,
ol {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
}
audio,
video {
  display: block;
}
img {
  display: inline-block;
  border: none;
}
iframe {
  border: none;
}
pre,
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: inherit;
}
a {
  background-color: transparent;
  text-decoration: none;
  color: inherit;
}
abbr {
  border: none;
  text-decoration: none;
}
b,
strong {
  font-weight: inherit;
}
i,
em {
  font-style: inherit;
}
dfn {
  font-style: inherit;
}
mark {
  background-color: transparent;
  color: inherit;
}
small {
  font-size: inherit;
}
button,
input,
optgroup,
select,
textarea {
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 0;
  box-shadow: none;
  background-color: transparent;
  font: inherit;
  color: inherit;
  letter-spacing: inherit;
}
button,
input {
  overflow: visible;
}
button,
select {
  text-align: left;
  text-transform: none;
}
button,
[type='button'],
[type='reset'],
[type='submit'] {
  cursor: pointer;
  appearance: none;
}
textarea {
  resize: none;
  overflow-y: auto;
  overflow-x: hidden;
}
button::-moz-focus-inner,
[type='button']::-moz-focus-inner,
[type='reset']::-moz-focus-inner,
[type='submit']::-moz-focus-inner {
  border: none;
  padding: 0;
}
button:-moz-focusring,
[type='button']:-moz-focusring,
[type='reset']:-moz-focusring,
[type='submit']:-moz-focusring {
  outline: none;
}
[type='number']::-webkit-inner-spin-button,
[type='number']::-webkit-outer-spin-button {
  height: auto;
}
[type='search']::-webkit-search-decoration {
  appearance: none;
}
[type='search'] {
  outline: none;
}
fieldset {
  padding: 0;
  margin: 0;
  border: none;
}
legend {
  display: block;
  padding: 0;
  white-space: normal;
}
select {
  appearance: none;
}
select::-ms-expand {
  display: none;
}
::-webkit-input-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity 0.3s;
}
::-moz-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity 0.3s;
}
:-moz-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity 0.3s;
}
:-ms-input-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity 0.3s;
}
:focus::-webkit-input-placeholder {
  opacity: 0;
}
:focus::-moz-placeholder {
  opacity: 0;
}
:focus:-moz-placeholder {
  opacity: 0;
}
:focus:-ms-input-placeholder {
  opacity: 0;
}
svg {
  display: inline-block;
  width: 100%;
  height: 100%;
}
[hidden] {
  display: none;
}
:disabled,
.disabled {
  cursor: not-allowed;
}
::-ms-clear {
  display: none;
}
.visually-hidden {
  position: absolute;
  z-index: -$z-index1;
  width: 0;
  height: 0;
  margin: 0;
  border: none;
  overflow: hidden;
}
body {
  font: 14px/24px "lora-regular", sans-serif, arial;
  color: #797979;
}
.container {
  width: 98%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}
h1 {
  font: 48px/1 "antonio-regular", sans-serif, arial;
  color: #000;
}
h2 {
  font: 48px/1 "antonio-regular", sans-serif, arial;
  color: #000;
}
h3 {
  font: 48px/1 "antonio-regular", sans-serif, arial;
  color: #000;
}
h4 {
  font: 48px/1 "antonio-regular", sans-serif, arial;
  color: #000;
}
h5 {
  font: 48px/1 "antonio-regular", sans-serif, arial;
  color: #000;
}
h6 {
  font: 48px/1 "antonio-regular", sans-serif, arial;
  color: #000;
}
.btn {
  display: inline-block;
  height: 40px;
  padding: 0 30px;
  font: 15px/39px "antonio-regular", sans-serif, arial;
  letter-spacing: 0.3px;
  text-transform: uppercase;
  color: #fff;
  border-radius: 25px;
}
.btn_color_transparent {
  background-color: transparent;
  border: 2px solid #fff;
}
.btn_color_orange {
  background-color: #ef4a09;
}
.btn_color_yellow {
  color: #000;
  background-color: #efba39;
}
.page__hero {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  padding: 30px 0;
  background-color: rgba(0,0,0,0.7);
}
.page__hero::after,
.our-menu-item__hero::after,
.footer::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.3);
}
.page__caption,
.our-menu-item__title {
  font-size: 72px;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
}
.page__caption span,
.our-menu-item__title span,
.our-menu-item__subtitle {
  display: inline-block;
  margin-bottom: -6px;
  font: 120px/1 "great-vibes-regular", sans-serif, arial;
  text-transform: lowercase;
  color: #efba39;
}
.page__inf_align_center {
  text-align: center;
}
.page__phone {
  margin-bottom: 12px;
  letter-spacing: 0.6px;
  font: 14px/1 "antonio-regular", sans-serif, arial;
  text-transform: uppercase;
  color: #fff;
}
.page__phone span {
  display: inline-block;
  margin-left: 6px;
  color: #efba39;
}
.social {
  display: inline-block;
  width: 110px;
}
.social ul {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.social a {
  display: block;
  width: 30px;
  height: 30px;
  text-align: center;
  background-color: #797979;
  border-radius: 50%;
}
.social a svg {
  width: 45%;
  fill: #fff;
}
.social a:hover {
  background-color: #efba39;
}
.social a:hover svg {
  fill: #000;
}
.page__main {
  padding: 150px 0;
}
.page__heading {
  max-width: 770px;
  margin: 0 auto;
  text-align: center;
}
.page__subtitle {
  display: inline-block;
  margin-bottom: 50px;
  font: 14px/1 "antonio-regular", sans-serif, arial;
  text-transform: uppercase;
  color: #000;
}
.page__title {
  font-size: 48px;
  line-height: 1;
  text-transform: uppercase;
  color: #000;
}
.page__title + p {
  margin-top: 18px;
}
.page__our-menu .page__hero {
  background: url("img/content/our-menu-list/andersen-jensen-637701-unsplash_2000px.png") no-repeat top/cover;
}
.our-menu .page__heading {
  margin-bottom: 45px;
}
.our-menu-item__hero {
  position: relative;
  z-index: 1;
  margin-bottom: 52px;
  padding: 157px 0;
  text-align: center;
  background-color: #797979;
}
.our-menu__item:not(:last-child) {
  margin-bottom: 45px;
}
.our-menu-item__cont {
  display: flex;
  flex-wrap: wrap;
}
.our-menu-item__dish {
  flex-basis: 50%;
}
@supports (display: flex) {
  .our-menu-item__dish:nth-child(odd) {
    padding-right: 15px;
  }
}
@supports (display: flex) {
  .our-menu-item__dish:nth-child(even) {
    padding-left: 15px;
  }
}
.our-menu-item__dish:not(:nth-last-child(-n+2)) {
  margin-bottom: 40px;
}
.our-menu-item__name-and-price {
  margin-bottom: 8px;
  font: 18px/1 "antonio-regular", sans-serif, arial;
}
.our-menu-item__name-and-price::after {
  content: '';
  display: table;
  clear: both;
}
.our-menu-item__name {
  float: left;
  color: #000;
}
.our-menu-item__price {
  float: right;
  color: #797979;
}
.page__our-menu footer {
  background: url("img/content/our-menu-list/andersen-jensen-637701-unsplash_2000px.png") no-repeat bottom/cover;
}
.page__team .page__hero {
  background: #797979 url("img/content/team/joshua-rodriguez-422768-unsplash_1920px.png") no-repeat top center/cover;
}
.page__team footer {
  background: #797979 url("img/content/team/joshua-rodriguez-422768-unsplash_1920px.png") no-repeat bottom center/cover;
}
.page__team footer::after {
  background-color: rgba(0,0,0,0.8);
}
.team__tabs {
  display: flex;
  justify-content: center;
  padding: 20px 0 44px 0;
}
.team__tab {
  position: relative;
  z-index: 1;
}
.team__tab_state_active {
  color: #000;
}
.team__tab_state_active::after {
  content: '';
  position: absolute;
  bottom: 5px;
  left: 0;
  z-index: -1;
  display: block;
  width: 100%;
  height: 5px;
  background-color: #efba39;
}
.team__tab:not(:last-child) {
  margin-right: 28px;
}
.team__members {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 50px;
}
.team__member {
  flex-basis: 370px;
  text-align: center;
  border: 1px solid #797979;
}
@media (min-width: 1024px) {
  .team__member:nth-child(-n+3) {
    margin-bottom: 30px;
  }
  .team__member:not(:nth-child(3n)) {
    margin-right: 30px;
  }
}
.team-member__ava-container {
  position: relative;
  z-index: 1;
  padding-top: 300px;
  background-color: #797979;
  overflow: hidden;
}
.team-member__ava {
  position: absolute;
  top: -25%;
  left: 0;
  z-index: 1;
  width: 100%;
}
.team-member__cont {
  padding: 30px 0;
}
.team-member__desc {
  margin-bottom: 12px;
}
.team-member__name {
  margin-bottom: 5px;
  font: 18px/1 "antonio-regular", sans-serif, arial;
  color: #000;
}
.team-member__pos {
  font-size: 12px;
}
.team__paginator {
  width: 175px;
  margin: 0 auto;
  text-align: center;
}
.team__paginator::after {
  content: '';
  display: table;
  clear: both;
}
.team__paginator a {
  float: left;
  width: 40px;
  height: 40px;
  font: 14px/1 "antonio-regular", sans-serif, arial;
  text-align: center;
  line-height: 40px;
  border: 1px solid #797979;
  border-radius: 50%;
}
.team__paginator .active {
  color: #000;
  background-color: #efba39;
  border-color: #efba39;
}
.team__paginator a:not(:last-child) {
  margin-right: 5px;
}
.team__back svg,
.team__forward svg {
  width: 14px !important;
  fill: #797979;
}
.team__back:hover svg,
.team__forward:hover svg {
  fill: #000;
}
.page__reserv .page__hero {
  background: url("img/content/reservation/florencia-viadana_2000px.png") no-repeat center/cover;
}
.reserv__row {
  position: relative;
  z-index: 1;
  display: flex;
  margin-top: 39px;
  padding: 0 70px;
  background: url("img/content/reservation/david-clarke_1200px.png") no-repeat center/cover;
}
.reserv__row::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.7);
}
.reserv__form-container {
  padding-right: 70px;
}
.reserv__form {
  margin-top: 64px;
}
.reserv-form__nb {
  margin-bottom: 22px;
  padding-right: 50px;
  color: #fff;
}
.reserv-form__nb span {
  color: #efba39;
}
.reserv-form__rows {
  margin-bottom: 30px;
}
.reserv-form__row {
  display: flex;
}
.reserv-form__row:not(:last-child) {
  margin-bottom: 31px;
}
.reserv-form__col_size_md {
  flex-basis: 50%;
}
.reserv-form__col_size_md:nth-child(odd) {
  padding-right: 5px;
}
.reserv-form__col_size_md:nth-child(even) {
  padding-left: 5px;
}
.reserv-form__col_size_lg {
  flex-basis: 100%;
}
.reserv-form__label {
  display: block;
  margin-bottom: 8px;
  font: 14px/1 "antonio-regular", sans-serif, arial;
  letter-spacing: 0.7px;
  text-transform: uppercase;
  color: #fff;
}
.reserv-form__input {
  width: 100%;
  padding: 8px 20px;
  font-size: 12px;
  background-color: #fff;
}
.reserv__map {
  width: 500px;
  height: 420px;
  transform: translateY(70px);
}
.reserv-form__btn-submit {
  padding: 0 36px;
  line-height: 44px;
}
.page__reserv footer {
  background: url("img/content/reservation/florencia-viadana_2000px.png") no-repeat bottom/cover;
}
.page__cont .page__hero {
  background: url("img/content/contact/rob-bye-141857-unsplash_1920px.png") no-repeat center/cover;
}
.page__cont footer {
  background: url("img/content/contact/rob-bye-141857-unsplash_1920px.png") no-repeat bottom/cover;
}
.header {
  padding: 0 46px;
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  flex: 2;
  font-size: 38px;
  line-height: 1;
  text-transform: uppercase;
  color: #fff;
}
.header__logo span {
  display: block;
  padding-left: 3px;
  font-size: 10px;
}
.mainnav__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: 42px;
}
.mainnav__item:not(:last-child) {
  margin-right: 42px;
}
.mainnav__link {
  font: 14px/1 "antonio-regular", sans-serif, arial;
  letter-spacing: 0.6px;
  text-transform: uppercase;
  color: #fff;
}
.mainnav__link:hover {
  color: #efba39;
}
.footer {
  position: relative;
  z-index: 1;
  padding: 100px 50px 95px 50px;
  background-color: rgba(0,0,0,0.7);
}
.footer::after {
  background-color: rgba(0,0,0,0.8);
}
.footer__inner {
  display: flex;
  justify-content: space-between;
}
.footer__logo {
  font-size: 38px;
  line-height: 1;
  text-transform: uppercase;
  color: #fff;
}
.footer__logo span {
  display: block;
  padding-left: 3px;
  font-size: 10px;
}
.footer__list-inf {
  display: flex;
  padding: 0 18px;
}
.footer__item {
  flex-basis: 150px;
  font-size: 12px;
  color: #fff;
}
.footer__item p {
  font-size: 12px;
}
.footer__item:not(:last-child) {
  margin-right: 150px;
}
.footer__caption {
  margin-bottom: 18px;
  font-size: 12px;
  text-transform: uppercase;
  color: #797979;
}
